import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController, ModalController, ViewController, LoadingController, AlertController, Searchbar, ToastController } from 'ionic-angular';
import { SoapService } from '../soap.service';

import { Storage } from '@ionic/storage';
import { api_base_url, api_user, api_pass, sc_code } from '../../config';
/**
 * Generated class for the IpcContactListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ipc-contact-list',
  providers: [SoapService],
  templateUrl: 'ipc-contact-list.html',
})
export class IpcContactListPage {
  IPCContactUserDataTPK: any;
  idLevel: any;
  isLoading: boolean = true;
  loading: any;
  userdataTPK: any;
  batasAtas = 1;
  batasBawah = 20;
  questionList: Array<any> = [];
  rate: Array<any> = [];
  modul:any;

  isLoadingBadges: boolean = true;
  badgesList: any;
  fakeUsers: Array<any> = new Array(5);

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public popoverCtrl: PopoverController,
    public viewCtrl: ViewController,
    public soapService: SoapService,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public alertCtrl: AlertController,
    public modalCtrl: ModalController,
    public toastCtrl: ToastController
  ) {
    this.modul = navParams.get('modul');
    // console.log(this.modul);
  }

  ionViewDidEnter() {
    this.questionList = [];
    this.storage.get('userdataTPK').then((val) => {
      this.userdataTPK = val;
      this.getBadges();
      this.getDataUserIpcc(val);
      this.getQuestionList('first', '', 'loading');
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IpcContactListPage');
  }

  getBadges() {
    this.isLoadingBadges = true;
    this.soapService
      .post(api_base_url, 'eoffice_countbadges', {
        fStream: JSON.stringify(
          {
            usernameEDI: api_user,
            passwordEDI: api_pass,
            iduser: this.userdataTPK['data']['IDUSER'],
            idjabatan: this.userdataTPK['data']['IDJABATAN'],
            nipp: this.userdataTPK['data']['NIPP']
          }
        )
      }).then(result => {

        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.badgesList = responData['data'];
          // console.log(this.badgesList);
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
        this.isLoadingBadges = false;
      })
      .catch(error => {
        // console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Gagal Mendapatkan Notifikasi, Periksa Koneksi Internet Anda.',
          duration: 3000,
          position: 'bottom'
        });
        //toast.present();    
        this.isLoadingBadges = false;
      });
  }

  getDataUserIpcc(val) {
    this.soapService
      .post(api_base_url, 'eoffice_bypass_wso', {
        fStream: JSON.stringify(
          {
            sc_type: 'ipccuser',
            sc_code: sc_code,
            data: {
              id_user: val['data']['IDUSER'],
              nipp: val['data']['NIPP'],
              modul: this.modul
            }
          }
        )
      }).then(result => {
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          this.storage.set('userdataIPCContact', responData['data']);
          this.IPCContactUserDataTPK = responData['data'];
          this.setLevel(responData['data']['ID_LEVEL']);
          // console.log(this.IPCContactUserDataTPK);
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }
      })
      .catch(error => {
        // console.log(error);
        let alert = this.alertCtrl.create({
          title: '',
          subTitle: 'Gagal Mendapatkan Data User IPC Contact, Periksa Koneksi Internet Anda.',
          buttons: ['OK']
        });
        alert.present();
      });
  }

  setLevel(val) {
    this.idLevel = val;
  }

  getQuestionList(type, functionName, load) {
    // let myLoading = this.loadingCtrl.create({
    //   spinner: 'dots',
    //   content: "Mohon Tunggu...",       
    //   cssClass: 'transparent', 
    //   dismissOnPageChange:true
    // });
    if (load == 'loading') {
      this.isLoading = true;
    }        
    
    this.soapService
      .post(api_base_url, 'eoffice_masalah_personal_list_ipcc', {
        fStream: JSON.stringify(
          {
            "usernameEDI": api_user,
            "passwordEDI": api_pass,
            "nipp": this.userdataTPK['data']['NIPP'],
            "atas": this.batasAtas,
            "bawah": this.batasBawah,
            "modul" : this.modul
          }
        )
      }).then(result => {
        result = String(result).replace(/\n/g, " ");
        var responData = JSON.parse(String(result));
        // console.log(responData);
        if (responData['rcmsg'] == "SUCCESS") {
          if (type == 'refresh' && functionName != '') {
            this.questionList = [];
            this.rate = [];
          }
          if (responData['data'].length > 0 && !this.isEmptyObject(responData['data'][0])) {
            for (var i = 0; i < responData['data'].length; i++) {
              this.questionList.push(responData['data'][i]);
              this.rate.push(responData['data'][i]['RATE']);
            }
          }
          // console.log(this.questionList);
          // console.log(this.rate);
        } else {
          let toast = this.toastCtrl.create({
            message: 'Mohon Maaf Sedang Terjadi Kesalahan, Coba Beberapa Saat Lagi.',
            duration: 3000,
            position: 'bottom'
          });
          toast.present();
        }

        if (type == 'first' && functionName == '') {

        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }
        //this.isLoading = false;  
        // if (load == 'loading') {
        //   myLoading.dismiss();
        // }
        this.isLoading = false;

      })
      .catch(error => {
        // console.log(error);
        let toast = this.toastCtrl.create({
          message: 'Terjadi Masalah Koneksi, Silahkan Coba Kembali.',
          duration: 3000,
          position: 'bottom'
        });
        toast.present();

        //this.isLoading = false;
        if (type == 'first' && functionName == '') {
        } else if (type == 'infinite' && functionName != '') {
          functionName.complete();
        } else if (type == 'refresh' && functionName != '') {
          functionName.complete();
        }
        // if (load == 'loading') {
        //   myLoading.dismiss();
        // }
        this.isLoading = false;
      });
  }

  doInfinite(infiniteScroll) {
    if (this.questionList.length >= 10) {
      this.batasAtas = this.batasBawah + 1;
      this.batasBawah = this.batasBawah + 20;
      this.getQuestionList('infinite', infiniteScroll, 'n0 loading');
    } else {
      infiniteScroll.complete();
    }
  }

  doRefresh(refresher) {
    this.batasAtas = 1;
    this.batasBawah = 20;

    this.getQuestionList('refresh', refresher, 'loading');
  }

  presentPopover(myEvent) {
    let popover = this.popoverCtrl.create("MenuIpcContactPage", {
      "IPCContactUserData": this.IPCContactUserDataTPK,
      "badges": this.badgesList['JUMLAH_MASALAH_IPCC']
    }, { cssClass: 'my-popover' });
    popover.present({
      ev: myEvent
    });
  }

  openModal(modalPage) {
    let modal = this.modalCtrl.create(modalPage, {
      modul: this.modul
    }, {});
    modal.present();

    modal.onDidDismiss(data => {
      // console.log(data);
      this.questionList = [];
      this.batasAtas = 1;
      this.batasBawah = 20;
      this.getQuestionList('first', '', 'no loading');
    });
  }


  goToDetail(data) {
    this.navCtrl.push('IpcContactDetailPage', {
      data: data,
      modul: this.modul
    });
  }

  isEmptyObject(obj) {
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        return false;
      }
    }
    return true;
  }

  parse(val) {
    var intValue = parseInt(val);

    if (intValue > 0) {
      return true;
    } else {
      return false;
    }
  }
}



